




















import Swal from 'sweetalert2';
import { Action, State } from 'vuex-class';
import { Component, Vue } from 'vue-property-decorator';
import { ILoginPayload, ITags } from '@/types/interface';
import Banner from '../../components/Banner.vue';
import BaseFooter from '../../components/BaseFooter.vue';
import SubscribeLabelMap from '../../components/SubscribeLabelMap.vue';

@Component({
  components: {
    Banner,
    BaseFooter,
    SubscribeLabelMap,
  },
})

export default class EditSubscribeItem extends Vue {
  @Action('getTag', { namespace: 'tags' }) getTag!: () => Promise<void>;
  @Action('getAllTag', { namespace: 'tags' }) getAllTag!: () => void;
  @Action('addTags', { namespace: 'tags' }) addTags!: (list: string[] | []) => Promise<ILoginPayload>;
  @State('tags', { namespace: 'tags' }) tags!: ITags[] | [];
  @State('selectedTags', { namespace: 'tags' }) selectedTags!: ITags[];

  listOne: string[] | [] = [];

  get isMemberProcess() {
    return this.$route.query.process === 'member';
  }

  link() {
    this.$router.push('/member');
  }

  next() {
    this.addTags(this.listOne)
      .then((data: ILoginPayload) => {
        if (data.Code === '0000') {
          if (this.isMemberProcess) {
            Swal.fire({
              toast: true,
              width: 300,
              position: 'top',
              showConfirmButton: false,
              showCloseButton: true,
              background: '#EDFAF5',
              buttonsStyling: false,
              customClass: {
                closeButton: 'success-toast-close-btn',
                popup: 'success-toast-popup',
                title: 'success-toast-title',
              },
              title: '儲存成功',
            });
          } else {
            this.$router.push('/member');
          }
        }
      });
  }

  created() {
    this.getAllTag();
    this.getTag()
      .then(() => {
        if (this.selectedTags.length !== 0) this.listOne = this.selectedTags.map((d: ITags) => d.value);
      });
  }
}
